  form button {
    height:38px;
    padding: 0.5rem;
    color: #ff6f47;
    background: #f7fffe;
    cursor: pointer;
    border: none;
    transition: all 0.3s ease;
  }
  form button:hover {
    background: #ff6f47;
    color: white;
  }

  .todo{
    display: flex;
    flex-basis: 100%;
    margin-top: 10px;
    margin-bottom: 20px;
  }
  .todo-item{
    background-color: white;
    padding: 0rem 0.5rem;
    line-height: 40px;
    width: auto;
  }
  .completed-todo{
    background-color: green;
    color:white;
    
  }
  .todo-item button{
      padding: 0px;
  }

  /* li button{
