  ul .complete-todo,button .fa-check{
    background-color: rgb(112, 219, 112);
    color:white;
    border-radius: 0px;
  }
ul .delete-todo, button .fa-trash{
  background-color: red;
  color: white;
  border-radius: 0px;
}
.completed{
  opacity: 0.5;
}